@keyframes blink {
	0% {
		border-right: 3px solid transparent;
	}
	100% {
		border-right: 3px solid #fcee0a;
	}
}

@keyframes pulse {
	0% {
		filter: opacity(0.25);
	}
	30% {
		filter: opacity(1);
	}
	100% {
		filter: opacity(1);
	}
}

@keyframes fade-in {
	0% {
		opacity: 0;
	}
	65% {
		opacity: 0;
	}
	100% {
		opacity: .5;
	}
}

@keyframes dot-fade-in {
	0% {
		opacity: 0;
		transform: scale(0.8);
		width: 40px;
	}
	25% {
		opacity: .50;
		transform: scale(1);
		width: 20px;
	}
	100% {
		opacity: 1;
	}
}
